import React from "react";
import { Link } from "react-router-dom";
import svideodata from "./Svdeodata";

function Svideo() {
  return (
    <>
      {svideodata.map((e) => (
        <div className="row px-2 mt-2">
          <div className="col-lg-5">
            <Link to={`/plays/${e.id}`}>
              <img src={e.img} className="img-fluid" />
            </Link>
          </div>
          <div className="col-lg-7">
            <h6 className="text-white">{e.title}</h6>
            <p> {e.channel}</p>
            <div className="d-flex align-items-center ">
              <p className="me-3">{e.views}</p>
              <p>{e.duration}</p>
            </div>
          </div>
        </div>
      ))}
    </>
  );
}

export default Svideo;
