import React from "react";
import "./Explore.css";
import explore from "./Explorejsdata";
import trending from "./Trendingdatalist";
import { Link } from "react-router-dom";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
function Explore() {
  return (
    <div className="Explore">
      <div className="row ps-5 pt-3">
        {explore.map((e) => (
          <div className="col-lg-3 box mx-1">
            <img src={e.img} alt="" className="img-fluid" />
            <p>{e.title}</p>
          </div>
        ))}
      </div>

      <h4 className="text-start ps-5 pt-4">Trending Videos</h4>

      {trending.map((e) => (
        <div className="row ps-5 py-2">
          <div className="col-3">
            <Link to={`/plays/${e.id}`}>
              <img src={e.img} className="img-fluid" alt="" />
            </Link>
          </div>
          <div className="col-9 ss">
            <h5>{e.titile}</h5>
            <div className="d-flex">
              <p>{e.channel}</p>
              <p className="px-2">{e.views}</p>
              <p>{e.duration}</p>
            </div>
            <p>{e.description}</p>
          </div>
        </div>
      ))}
    </div>
  );
}

export default Explore;
