import React from "react";
import "./Home.css";
import top from "./Data";
import VideoCard from "./VideoCard";
import vdata from "./VideoData";
import Avatar from "@mui/material/Avatar";
import { Routes, Route, Link } from "react-router-dom";

function Home() {
  return (
    <div className="home ">
      <div className="top ">
        {top.map((k) => (
          <button>{k.name}</button>
        ))}
      </div>

      <div className="row pt-3 ps-3 pe-1 mb-0 pe-3 ">
        {vdata.map((a) => (
          <div className="col-lg-3 col-md-4">
            <div className="videos">
              <div className="imgbox">
                <Link to={`/plays/${a.id}`}>
                  <img src={a.img} className="img-fluid" />
                </Link>
              </div>

              <div className="content">
                <div className="row pt-2">
                  <div className="col-lg-2">
                    <Avatar className="av" alt="Cindy Baker" src={a.avatar} />
                  </div>
                  <div className="col-lg-10 ">
                    <h6>{a.title}</h6>
                    <p className="t">{a.channel}</p>
                    <div className="d-flex align-items-center ">
                      <p className="me-3">{a.views}</p>
                      <p>{a.duration}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Home;
