import { useState } from "react";
import MenuIcon from "@mui/icons-material/Menu";
import logo from "./logo.png";
import "./Header.css";
import SearchIcon from "@mui/icons-material/Search";
import MicIcon from "@mui/icons-material/Mic";
import VideoCallOutlinedIcon from "@mui/icons-material/VideoCallOutlined";
import AppsOutlinedIcon from "@mui/icons-material/AppsOutlined";
import NotificationsNoneOutlinedIcon from "@mui/icons-material/NotificationsNoneOutlined";
import Avatar from "@mui/material/Avatar";
import ExploreIcon from "@mui/icons-material/Explore";
import SlowMotionVideoIcon from "@mui/icons-material/SlowMotionVideo";
import VideoStableIcon from "@mui/icons-material/VideoStable";
import VideoLibraryIcon from "@mui/icons-material/VideoLibrary";
import HomeIcon from "@mui/icons-material/Home";
import Home from "./Home";
import { Routes, Route, Link } from "react-router-dom";
import VideoPlay from "./VideoPlay";
import Short from "./Short";
import Vplays from "./Vplays";
import Explore from "./Explore";

function Header() {
  const [Sidebar, setSidebar] = useState(false);

  const showsidebar = () => setSidebar(!Sidebar);
  return (
    <div className="hh">
      <div className="bg sticky-top">
        <div className="container-fluid px-4 ">
          <div className="row">
            <div className="col-lg-3 col-sm-12 d-flex align-items-center justify-content-start dd">
              <MenuIcon
                fontSize="large"
                className="ico "
                onclick={showsidebar}
              />
              <Link to="/">
                <img src={logo} alt="logo" className="logo" />
              </Link>
            </div>
            <div className="col-lg-6 d-lg-flex align-items-center justify-content-center d-none d-sm-block ">
              <div className="search ">
                <div>
                  <input placeholder="Search " />
                </div>
                <div>
                  <SearchIcon className="search__icon ico" />
                </div>
              </div>
              <div className="mic">
                <MicIcon className="ico" />
              </div>
            </div>
            <div className="col-lg-3 d-none d-lg-block d-sm-none d-lg-flex align-items-center justify-content-end">
              <VideoCallOutlinedIcon className="icons" />
              <AppsOutlinedIcon className="icons" />
              <NotificationsNoneOutlinedIcon className="icons" />
              <Avatar
                className="av"
                alt="Cindy Baker"
                src="https://yt3.ggpht.com/XvwU7JfXVb1351RafqHaF_ieKr78K059gMJWm80Jsk2ydGrj82TDfn0yiox3tf08F2sK6sPN=s88-c-k-c0x00ffffff-no-rj-mo"
              />
            </div>
          </div>
        </div>
      </div>

      <div className="sidebar d-none d-sm-none d-lg-block">
        <ul>
          <li>
            <HomeIcon className="cc" />
            <Link to="/" className="h">
              Home
            </Link>
          </li>
          <li>
            <ExploreIcon className="cc" />
            <Link to="/Explore/">Explore</Link>
          </li>
          <li>
            <SlowMotionVideoIcon className="cc" />
            <Link to="/shorts/ " className="h">
              Shorts
            </Link>
          </li>
          <li>
            <VideoStableIcon className="cc" />
            <a>Subcriptions</a>
          </li>
          <li>
            <VideoLibraryIcon className="cc" />
            <a>Library</a>
          </li>
        </ul>
      </div>

      <div className={Sidebar ? "menu active" : "menu"}>
        <ul>
          <li>
            <a>
              <MenuIcon fontSize="large" className="ico bg-primary" />
            </a>
          </li>
          <li>
            <a href="">Home</a>
          </li>
          <li>
            <a href="">Explore</a>
          </li>
          <li>
            <a href="">Shorts</a>
          </li>
          <li>
            <a href="">Subscrptioin</a>
          </li>
          <li>
            <a href="">Library</a>
          </li>
        </ul>
      </div>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/shorts/" element={<Short />} />
        <Route path="/Explore/" element={<Explore />} />
        <Route path="/plays/:id" element={<Vplays />} />
      </Routes>
    </div>
  );
}

export default Header;
